body {
  font-family: Roboto, IRANSans, sans-serif !important;
  min-height: 100vh; }

.hidden {
  display: none; }

.cm-wrapper label {
  margin-bottom: 0; }

.hasChild > a:after {
  content: "";
  border-right: 4px dashed;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent; }

[contenteditable=true]:empty:before {
  content: attr(placeholder);
  display: block; }

.cm-wrapper {
  position: relative;
  margin-bottom: 16px; }

.cm-section {
  position: relative; }

.cm-content {
  min-height: 40px;
  padding: 8px;
  position: relative;
  right: 32px; }
  .cm-content:focus {
    outline: none; }
  .cm-content video, .cm-content img {
    width: calc(100% - 26px); }

.cm-btn-control {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0; }
  .cm-btn-control .btn-create {
    border-radius: 50vh;
    padding: 8px;
    border: 1px solid #656565;
    background-color: #ffffff;
    color: #656565;
    cursor: pointer; }
    .cm-btn-control .btn-create:hover, .cm-btn-control .btn-create:active, .cm-btn-control .btn-create:focus {
      color: #424242;
      border-color: #424242;
      outline: none; }
  .cm-btn-control .btn-create-wrapper {
    position: relative;
    height: 100%;
    display: flex;
    align-items: center; }
    .cm-btn-control .btn-create-wrapper .btn-create {
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer; }
      .cm-btn-control .btn-create-wrapper .btn-create i {
        transition: transform 400ms ease-in-out; }
      .cm-btn-control .btn-create-wrapper .btn-create.close i {
        transition: transform 400ms ease-in-out;
        transform: rotate(45deg); }

.create-section-list {
  display: none;
  list-style: none;
  margin: 0;
  padding: 0 0 0 4px;
  margin-right: 40px;
  background-color: white; }
  .create-section-list.show {
    display: flex; }
  .create-section-list li {
    display: inline-block; }
    .create-section-list li button {
      display: flex;
      align-items: center;
      padding: 8px;
      border: 1px solid #656565;
      color: #656565;
      background-color: #ffffff;
      border-radius: 50vh;
      cursor: pointer; }
      .create-section-list li button:hover, .create-section-list li button:active, .create-section-list li button:focus {
        outline: none;
        color: #424242;
        border-color: #424242; }
      .create-section-list li button i {
        font-size: 18px; }

.text-editor-wrapper {
  padding: 0; }
  .text-editor-wrapper .text-editor-item:first-child {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px; }
  .text-editor-wrapper .text-editor-item:last-child {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    border-right: 1px solid #000000; }

.text-editor-item {
  border: none;
  width: 48px;
  height: 48px;
  border-left: 1px solid #000000;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  background-color: #424242;
  color: #ffffff; }

.custom-icons {
  font-style: normal; }
  .custom-icons.ci-18 {
    font-size: 18px; }
  .custom-icons.ci-b {
    font-weight: bold; }

.flexbox {
  display: flex; }
  .flexbox.flexbox-align-center {
    align-items: center; }
  .flexbox.flexbox-just-spacebetween {
    justify-content: space-between; }

.modal-body {
  max-height: calc(100vh - 128px);
  height: 512px;
  overflow: hidden;
  overflow-y: auto; }
